import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { motion } from "framer-motion";
import "../css/master.css";
import ReactTextCollapse from "react-text-collapse/dist/ReactTextCollapse";
import { TEXT_COLLAPSE_OPTIONS } from "../App";

export default function Project(props) {
  var TEXT_C_O = Object.assign({}, TEXT_COLLAPSE_OPTIONS);

  TEXT_C_O.maxHeight = props.size || 500;

  return (
    <div class="container">
      <motion.h2
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 3 }}
      >
        {props.title}
      </motion.h2>

      {props.embed.map((video) => (
        <div>
          <iframe
            width="560"
            height="315"
            src={video.src}
            title="YouTube video player"
            frameborder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowfullscreen
          ></iframe>
        </div>
      ))}

      {props.imgs.map((img) => (
        <div>
          <img class="project_img" src={img.src} />
        </div>
      ))}

      <div>
        <strong>Soft Skills:</strong>
        <div>
          {props.soft.map((pgraph) => (
            <div>{pgraph}</div>
          ))}
        </div>
        <strong>Technologies:</strong>
        <div>
          {props.tech.map((pgraph) => (
            <div>{pgraph}</div>
          ))}
        </div>
      </div>
      <br />

      <div class="blurb">
        <ReactTextCollapse options={TEXT_C_O}>
          {props.blurb.map((pgraph) => (
            <div>
              {pgraph}
              <br />
              <br />
            </div>
          ))}
        </ReactTextCollapse>
      </div>
    </div>
  );
}
